<template lang="html">
  <div class="container iniciators__container" id="iniciatorsS">
    <Title lefText="Инициаторам"/>
    <div class="card__features">
      <div class="fix__block">

      </div>
      <h3 class="ml-3 mb-2" >Преимущества</h3>
      <ul class="ml-3 mt-2" style="margin-top: 60px;">
        <li>Создание собственных токенов проекта и проведение TGE</li>

        <li>Индивидуальный поиск инвестиций для пресейла, доступны контактные данные</li>

        <li>Возможность получения займа или гранта в инвестиционном экологическом фонде</li>

        <li>Создание криптокошелька</li>

        <li>Получение объективного рейтинга проекта и эдвайзеров</li>

        <li>PR-продвижение проектов в СМИ</li>
      </ul>
      <h3 class="ml-3 mb-2 mt-5">
        Перечень необходимых документов, обязательных для размещения проекта
      </h3>
      <ul class="docs__list" style="list-style-type:none;">
        <li>1. Whitepaper</li>
        <li>2. Onepager</li>
        <li>3. Pitch deck</li>
        <li>4. Business plan</li>
      </ul>
      <div class="row">
        <button
          v-on:click="setproject"
          v-bind:disabled="isSignUp"
          type="button"
          class="btn btn-outline-indigo"
          name="button"
          id="btn">
          Добавить проект
        </button>
      </div>
      <div class="row mb-5">
        <span class="red-text" v-if="!isSignUp">Чтобы добавить проект необходимо зарегистрироваться</span>

      </div>
      <h3 class="ml-3 mb-2 mt-5">Поиск инвестора на пресейл</h3>
      <p class="lead ml-3">Пользуйтесь основными и дополнительными услугами ресурса «Бизнес Платформа»:</p>
      <ul>
        <li>Подготовка необходимых документов,</li>
        <li>Поиск эдвайзеров,</li>
        <li>Экспресс-анализ проекта и экспертиза,</li>
        <li>Приоритетное размещение</li>
        <li>Еmail-рассылка по базе инвесторов</li>
      </ul>
    </div>
  </div>
</template>

<script>
import Title from './Title'
export default {
  name: 'Iniciators',
  components: {
    Title
  },
  methods: {
    setproject (){
        this.$router.push({ name: 'SetProjects'})
    }
  },
}
</script>

<style lang="css">
.iniciators__container{
  padding-top: 60px;
}
.card__features{
  width: 100%;
  margin: 0 auto;
}

.fix__block {
height: 52px;
}
.docs__list {
  margin-top: 20px;
}
</style>



